.list-container{
  width: 30vw;
  /* height: 90%; */
  background-color: white;
  border: 5px solid rgb(0, 66, 0);
  border-radius: 15px;
  /* overflow: hidden; */
  margin: 2%;
}
.list-container > h3{
  margin: 0;
  padding: 5px;
  position: sticky;
  top: 0;
  background-color: rgb(0, 66, 0);
  color: white;
}
.list-body {
  overflow-y: scroll;
  overflow-x: hidden;
  white-space: nowrap;
  height: 94%;
  width: 100%;
  
}
